@include respond-to(phones) {
  .burger {
    position: relative;
    width: 30px;
    height: 22px;
    cursor: pointer;
    margin-left: 19px;
    z-index: 2;
    transition-duration: 300ms;

    & span {
      display: block;
      position: absolute;
      width: 100%;
      opacity: 1;
      left: 0;
      transform: rotate(0deg);
      transition: .25s ease-in-out;
      height: 2px;
      background-color: $color-primary;


      &:nth-child(1) {
        top: 0;
      }

      &:nth-child(2), &:nth-child(3) {
        top: 10px;
      }

      &:nth-child(4) {
        top: 20px;
      }
    }
  }

  .header_light .burger span {
    background-color: #000000;
  }

  .burger.active {
    transform: rotate(90deg);
    top: 45px;
    right: 53px;
    position: absolute;

    & span {
      background-color: $color-primary;
    }

  }
}
