.contacts__link{
  @include georgia-normal();
  font-size: 20px;
  line-height: 115%;
  letter-spacing: 0.06em;
  color: $color-primary;
  text-decoration: none;
  display: flex;
  align-items: center;

  &:before{
    content: '';
    display: inline-block;
    margin-right: 20px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: auto;
  }

  &_mail:before {
    min-width: 40px;
    min-height: 32px;
    background-image: url('data:image/png;base64,');
  }

  &_phone:before {
    min-width: 40px;
    min-height: 32px;
    background-image: url('data:image/png;base64,');
  }

  &_spot:before {
    min-width: 22px;
    min-height: 32px;
    background-image: url('data:image/png;base64,');
  }
}

.contacts:first-child {
  margin-right: 160px;

  @include respond-to(laptops) {
    margin-right: 57px;
  }

  @include respond-to(phones) {
    margin-right: 0;
  }

  .section-title{
    margin-bottom: 43px;

    @include respond-to(phones) {
      margin-bottom: 45px;
    }
  }

  & .contacts__link{
    margin-bottom: 43px;
    position: relative;
    top: -6px;

    @include respond-to(laptops) {
      margin-bottom: 41px;
    }

    @include respond-to(phones) {
      display: flex;
      justify-content: center;
    }
  }
}

.contacts:last-child {
  .section-title{
    margin-bottom: 33px;

    @include respond-to(phones) {
      margin-left: auto;
      margin-right: auto;
    }
  }
  & .contacts__link{
    margin-bottom: 33px;

    @include respond-to(phones) {
      margin-bottom: 39px;
    }

    &:last-child {
      margin-bottom: 28px;
    }
  }
}

.contacts {

  .section-title {
    @media screen and (max-width: 766px) {
      font-size: 30px;
    }

    @include respond-to(phones) {
      font-size: 25px;
      line-height: 130%;
      letter-spacing: 0.06em;
    }
  }

  & .contacts__link {
    @media screen and (max-width: 766px) {
      font-size: 19px;
    }

    @include respond-to(phones) {
      font-size: 20px;
    }
  }
}

